<template>
    <Header/>
    <main class="main registration">
        <div class="container registration__wrap">
            <div class="registration__content">
                <h1 class="registration__title">Регистрация в&nbsp;голосовании</h1>
                <p class="registration__subtitle">Для участия в голосовании необходима регистрация</p>
                <form class="registration__form form" @submit="formSubmit">
                    <label class="form__item" v-for="(item, index) in fields" :key="index">
                        <input type="text" class="form__input" :name="item.name" v-model="item.value" required>
                        <span class="form__label">{{ item.label }}</span>
                    </label>
                    <button class="button button_black form__submit">Зарегестрироваться</button>
                </form>
            </div>
        </div>
    </main>
</template>

<script>

import Header from '../components/Header';

export default {
  name: 'Registration',
  data() {
    return {
        fields: [
            {name: 'user_name', value: '', label: 'Ваше имя'},
            {name: 'user_surname', value: '', label: 'Ваша фамилия'},
        ]
    }
  },
  mounted(){
    if(localStorage.isRegistered){
        const isRegistered = JSON.parse(localStorage.isRegistered).status;

        if(isRegistered){
            this.$router.push({
                path: '/main',
                replace: true
            });
        }
    }
  },
  methods: {
    formSubmit(){

        event.preventDefault();

        localStorage.user = JSON.stringify(this.fields);
        localStorage.isRegistered = JSON.stringify({status: true});

        this.$router.push({
            path: '/main',
            replace: true
        });
    },
  },
  components: {
    Header,
  }
}

</script>

